export default {
  props: ['companies'],
  data(){
    return {
      flippedCards:[],
      isSubscribed:true
    }
  },

  methods: {
    togglePopFlip(index){
      if(this.flippedCards.includes(index)){
        const pos = this.flippedCards.indexOf(index);
        console.log(pos)
          if (pos > -1) {
            this.flippedCards.splice(pos, 1);
          }
      }else{
         this.flippedCards.push(index)
      }

  }
  }

}
